    id: 182,
    titre: "The French Connection",
    titre2: "",
    description:
      "Le scénario s'inspire de l'enquête de policiers du New York City Police Department nommés « Popeye » Doyle et Buddy « Cloudy » Russo, qui sont en fait, dans la réalité, Eddie Egan (en) et Sonny Grosso. Ils ont enquêté sur les réseaux de la French Connection, organisation criminelle qui importait depuis la France la majeure partie de l'héroïne consommée aux États-Unis à l'époque du film. Par ailleurs, Egan et Grosso ont des rôles dans le film.\nIl est, depuis l'introduction de la classification de la Motion Picture Association of America, le premier film classé R (interdiction aux moins de 17 ans) de l'histoire du cinéma américain à avoir gagné l'Oscar du meilleur film.\nPremier grand succès de son réalisateur, il remporta en tout cinq Oscars, dont, outre celui du meilleur film, aussi du meilleur acteur pour Gene Hackman, du meilleur réalisateur, du meilleur montage et du meilleur scénario adapté pour Ernest Tidyman. Roy Scheider fut nommé pour l'Oscar du meilleur acteur dans un second rôle et le film pour l'Oscar de la meilleure photographie et l'Oscar du meilleur mixage de son. Par ailleurs, Tidyman reçut pour son scénario le prix Edgar-Allan-Poe et une nomination pour le Golden Globe. Le film est classé à la 93e place des meilleurs films du cinéma américain par l'American Film Institute.\nEn 2005, le film est sélectionné par le National Film Registry pour être conservé à la bibliothèque du Congrès aux États-Unis pour son « importance culturelle, historique ou esthétique »",
