import React, { useState, useEffect } from "react";
import "semantic-ui-css/semantic.min.css";
import { Grid, Segment, Header, Input, Button } from "semantic-ui-react";
import WigletsFilter from "../wiglets/WigletsFilter";
import films from "../constant/filmList";
import ActorField from "./ActorFields";
import GenreField from "./GenreField";
import MusicField from "./MusicField";
import CountryField from "./CountryField";
import DateField from "./DateField";
import TimeField from "./TimeField";
import "../App.css";

import RandomField from "./RandomField";

function Container() {
  const [generalSearch, setGeneralSearch] = useState("");
  const [listOfSearch, setlistOfSearch] = useState([]);
  const [listOfTagsSelected, setListOfTagsSelected] = useState([]);
  const [listOfAntiTagsSelected, setListOfAntiTagsSelected] = useState([]);
  const [dateA, setDateA] = useState(1900);
  const [dateB, setDateB] = useState(2025);
  const [durreeA, setDurreeA] = useState(0);
  const [durreeB, setDurreeB] = useState(240);
  const containsAll = (needles, haystack) =>
    needles.every(Set.prototype.has, new Set(haystack));

  useEffect(() => {
    const listOfS = findFilm(generalSearch);

    if (listOfTagsSelected.length > 0 || listOfAntiTagsSelected.length > 0) {
      const list = listOfS.filter((film) => {
        const listLabel = film.tag.map((l) => l.label);
        if (
          film &&
          film.tag &&
          listOfAntiTagsSelected.length > 0 &&
          containsAll(listOfAntiTagsSelected, listLabel)
        )
          return 0;
        if (film && film.tag && containsAll(listOfTagsSelected, listLabel))
          return 1;
        return 0;
      });
      setlistOfSearch(list);
    }
    // eslint-disable-next-line
  }, [
    listOfTagsSelected,
    generalSearch,
    dateA,
    dateB,
    durreeA,
    durreeB,
    listOfAntiTagsSelected,
  ]);

  function findFilm() {
    // filtre - //
    const listfilm1 = films.filter((obj) => {
      return (
        obj.année >= dateA &&
        obj.année <= dateB &&
        stringToHour(obj.time) >= durreeA &&
        stringToHour(obj.time) <= durreeB
      );
    });

    // filtre + //
    const listfilm = listfilm1.filter((obj) => {
      return (
        obj.id === generalSearch ||
        generalSearch === obj.année.toString() ||
        obj.director.toUpperCase().indexOf(generalSearch.toUpperCase()) >= 0 ||
        obj.titre.toUpperCase().indexOf(generalSearch.toUpperCase()) >= 0
      );
    });

    setlistOfSearch(listfilm);
    return listfilm;
  }

  function stringToHour(s) {
    var lol = 0;
    var res = "";

    if (s.includes("h")) {
      res = s.split("h");

      lol = parseFloat(res[0] * 60) + parseFloat(res[1]);
    } else {
      res = s.split("min");
      lol = parseFloat(res[0]);
    }

    return lol;
  }

  return (
    <Segment>
      <Header as="h1">RECHECHER UN FILM</Header>
      <Input
        size="massive"
        icon="search"
        placeholder="Recherche générale"
        onChange={(e, { value }) => setGeneralSearch(value)}
      />
      <Header as="h1">Choisis des filtres :</Header>
      <CountryField
        listOfTagsSelected={listOfTagsSelected}
        listOfAntiTagsSelected={listOfAntiTagsSelected}
        setListOfAntiTagsSelected={setListOfAntiTagsSelected}
        setListOfTagsSelected={setListOfTagsSelected}
      />
      <ActorField
        listOfTagsSelected={listOfTagsSelected}
        listOfAntiTagsSelected={listOfAntiTagsSelected}
        setListOfAntiTagsSelected={setListOfAntiTagsSelected}
        setListOfTagsSelected={setListOfTagsSelected}
      />
      <MusicField
        listOfTagsSelected={listOfTagsSelected}
        listOfAntiTagsSelected={listOfAntiTagsSelected}
        setListOfAntiTagsSelected={setListOfAntiTagsSelected}
        setListOfTagsSelected={setListOfTagsSelected}
      />
      <GenreField
        listOfTagsSelected={listOfTagsSelected}
        listOfAntiTagsSelected={listOfAntiTagsSelected}
        setListOfAntiTagsSelected={setListOfAntiTagsSelected}
        setListOfTagsSelected={setListOfTagsSelected}
      />
      <DateField setDateA={setDateA} setDateB={setDateB} />
      <TimeField setDurreeA={setDurreeA} setDurreeB={setDurreeB} />
      <RandomField listOfSearch={listOfSearch} />
      <Button color="green" onClick={() => findFilm()}>
        Valider
      </Button>
      <Grid>
        <Grid.Row stretched>
          <Grid.Column>
            <WigletsFilter listOfSearch={listOfSearch} />
          </Grid.Column>
        </Grid.Row>
      </Grid>
    </Segment>
  );
}

export default Container;
