import React, { useEffect } from "react";
import "semantic-ui-css/semantic.min.css";
import { Grid, Tab } from "semantic-ui-react";
import FilmCard from "./FilmCard";

function WigletsFilter(props) {
  const { listOfSearch } = props;

  useEffect(() => {});

  return (
    <Tab.Pane>
      <Grid>
        <Grid.Row columns={4} only="tablet computer">
          {listOfSearch &&
            listOfSearch.map((film) => {
              if (film) {
                return <FilmCard key={film.id} film={film} />;
              }
              return true;
            })}
        </Grid.Row>
        <Grid.Row columns={1} only="tablet mobile">
          {listOfSearch &&
            listOfSearch.map((film) => {
              if (film) {
                return <FilmCard key={film.id} film={film} />;
              }
              return true;
            })}
        </Grid.Row>
      </Grid>
    </Tab.Pane>
  );
}

export default WigletsFilter;
