import React, { useState } from "react";
import "semantic-ui-css/semantic.min.css";
import {
  Grid,
  Card,
  Image,
  Modal,
  Header,
  Label,
  Rating,
} from "semantic-ui-react";

import Cookies from "universal-cookie";

function FilmCard(props) {
  const { film } = props;

  const cookies = new Cookies();

  const [modalPath, setModalPath] = useState(false);
  const [heart, setHeart] = useState(cookies.get(film && film.titre));

  // cookies.set(film.titre, 1, { path: "/" });
  // console.log(cookies.get('myCat')); // Pacman

  return (
    <Grid.Column>
      <Modal
        closeIcon
        open={modalPath}
        onClose={() => setModalPath(false)}
        onOpen={() => setModalPath(true)}
        trigger={
          <Card color="red">
            <Card.Content>
              <Card.Header>{film && film.titre}</Card.Header>
              <Image size="small" src={film && film.image} />
              <Card.Meta></Card.Meta>
              <Card.Description>
                <strong></strong>
              </Card.Description>
            </Card.Content>
            <Card.Content extra>{film && film.director}</Card.Content>
          </Card>
        }
      >
        <Header icon="film" content={film && (film.titre2 || film.titre)} />
        <Modal.Content image>
          <Image size="medium" src={film && film.image} />
          <Modal.Description>
            <Header>
              <Header.Content>
                {film && film.titre} ({film && film.année})
                <Header.Subheader>{film && film.time}</Header.Subheader>
              </Header.Content>
            </Header>
            {/* <Header as="h5">{film.time}</Header> */}
            <p>{film && film.description}</p>
            <p>{film && film.description2}</p>
          </Modal.Description>
        </Modal.Content>
        <Modal.Actions>
          {film &&
            film.tag &&
            film.tag.map((tag, i) => {
              //const {label, icon} = tag
              return (
                <Label as="a" image key={i}>
                  <Image src={tag && tag.icon} />
                  {tag && tag.label}
                </Label>
              );
            })}
        </Modal.Actions>
      </Modal>
      <Rating
        icon="heart"
        defaultRating={heart}
        maxRating={3}
        onRate={(a, e) => {
          

          film &&
            cookies.set(film.titre, e.rating, {
              path: "/",
              maxAge: 31536000
            });
          //console.log("cookie",cookies.get(film.titre)); // Pacman
          setHeart(e.rating);
        }}
      />
    </Grid.Column>
  );
}

export default FilmCard;
